import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { VacationState, Vacation } from '../../types/store.types';

const initialState: VacationState = {
  vacations: [],
  currentPage: 1,
  totalPages: 1,
  loading: false,
  error: null
};

const vacationSlice = createSlice({
  name: 'vacation',
  initialState,
  reducers: {
    fetchVacationsStart: (state) => {
      state.loading = true;
      state.error = null;
    },
    fetchVacationsSuccess: (state, action: PayloadAction<{ 
      vacations: Vacation[],
      currentPage: number,
      totalPages: number 
    }>) => {
      state.loading = false;
      state.vacations = action.payload.vacations;
      state.currentPage = action.payload.currentPage;
      state.totalPages = action.payload.totalPages;
      state.error = null;
    },
    fetchVacationsFailure: (state, action: PayloadAction<string>) => {
      state.loading = false;
      state.error = action.payload;
    },
    updateVacationSuccess: (state, action: PayloadAction<Vacation>) => {
      state.vacations = state.vacations.map(vacation =>
        vacation.id === action.payload.id ? action.payload : vacation
      );
    },
    deleteVacationSuccess: (state, action: PayloadAction<number>) => {
      state.vacations = state.vacations.filter(vacation => vacation.id !== action.payload);
    },
    toggleFollowSuccess: (state, action: PayloadAction<{ 
      vacationId: number, 
      isFollowing: boolean 
    }>) => {
      state.vacations = state.vacations.map(vacation =>
        vacation.id === action.payload.vacationId
          ? { 
              ...vacation, 
              isFollowing: action.payload.isFollowing,
              followersCount: action.payload.isFollowing 
                ? vacation.followersCount + 1 
                : vacation.followersCount - 1
            }
          : vacation
      );
    },
    clearError: (state) => {
      state.error = null;
    }
  }
});

export const {
  fetchVacationsStart,
  fetchVacationsSuccess,
  fetchVacationsFailure,
  updateVacationSuccess,
  deleteVacationSuccess,
  toggleFollowSuccess,
  clearError
} = vacationSlice.actions;

export default vacationSlice.reducer; 