import React from 'react';
import { Card, CardContent, CardMedia, Typography, IconButton, Box } from '@mui/material';
import FavoriteIcon from '@mui/icons-material/Favorite';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import { Vacation } from '../../types/store.types';
import { useAppDispatch } from '../../hooks/redux';
import { toggleFollowSuccess } from '../../store/slices/vacationSlice';
import api from '../../services/api';

interface VacationCardProps {
  vacation: Vacation;
  isAdmin: boolean;
}

const VacationCard: React.FC<VacationCardProps> = ({ vacation, isAdmin }) => {
  const dispatch = useAppDispatch();

  const handleFollowToggle = async () => {
    try {
      await api.post(`/vacations/${vacation.id}/follow`);
      dispatch(toggleFollowSuccess({ 
        vacationId: vacation.id, 
        isFollowing: !vacation.isFollowing 
      }));
    } catch (error) {
      console.error('Error toggling follow:', error);
    }
  };

  return (
    <Card>
      <CardMedia
        component="img"
        height="200"
        image={vacation.imageUrl}
        alt={vacation.destination}
      />
      <CardContent>
        <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
          <Typography variant="h6" component="div">
            {vacation.destination}
          </Typography>
          {!isAdmin && (
            <IconButton onClick={handleFollowToggle} color="primary">
              {vacation.isFollowing ? <FavoriteIcon /> : <FavoriteBorderIcon />}
            </IconButton>
          )}
        </Box>
        <Typography variant="body2" color="text.secondary" mb={2}>
          {vacation.description}
        </Typography>
        <Typography variant="body2" mb={1}>
          תאריכים: {new Date(vacation.startDate).toLocaleDateString()} - {new Date(vacation.endDate).toLocaleDateString()}
        </Typography>
        <Typography variant="h6" color="primary" mb={1}>
          ₪{vacation.price.toLocaleString()}
        </Typography>
        <Typography variant="body2" color="text.secondary">
          {vacation.followersCount} עוקבים
        </Typography>
      </CardContent>
    </Card>
  );
};

export default VacationCard; 