import api from './api';
import { Vacation } from '../types/store.types';

interface VacationsResponse {
  vacations: Vacation[];
  currentPage: number;
  totalPages: number;
}

export const getVacations = async (page: number = 1): Promise<VacationsResponse> => {
  const response = await api.get<VacationsResponse>(`/vacations?page=${page}`);
  return response.data;
};

const vacationService = {
  getVacations,
  getVacation: async (id: string): Promise<Vacation> => {
    const response = await api.get<Vacation>(`/vacations/${id}`);
    return response.data;
  },
  createVacation: async (formData: FormData): Promise<Vacation> => {
    const response = await api.post<Vacation>('/vacations', formData);
    return response.data;
  },
  updateVacation: async (id: string, formData: FormData): Promise<Vacation> => {
    const response = await api.put<Vacation>(`/vacations/${id}`, formData);
    return response.data;
  },
  deleteVacation: async (id: string): Promise<void> => {
    await api.delete(`/vacations/${id}`);
  },
  toggleFollow: async (id: number): Promise<Vacation> => {
    const response = await api.post<Vacation>(`/vacations/${id}/follow`);
    return response.data;
  }
};

export { vacationService as default }; 