import React, { useEffect } from 'react';
import { useAppSelector, useAppDispatch } from '../../hooks/redux';
import { fetchVacationsStart, fetchVacationsSuccess, fetchVacationsFailure } from '../../store/slices/vacationSlice';
import VacationCard from './VacationCard';
import { Container, Grid, Pagination, Box, CircularProgress } from '@mui/material';
import vacationService from '../../services/vacation.service';

const VacationList: React.FC = () => {
  const dispatch = useAppDispatch();
  const { vacations, currentPage, totalPages, loading } = useAppSelector(state => state.vacation);
  const { user } = useAppSelector(state => state.auth);

  useEffect(() => {
    const loadVacations = async () => {
      try {
        dispatch(fetchVacationsStart());
        const data = await vacationService.getVacations(currentPage);
        dispatch(fetchVacationsSuccess(data));
      } catch (error) {
        dispatch(fetchVacationsFailure(error instanceof Error ? error.message : 'Failed to fetch vacations'));
      }
    };

    loadVacations();
  }, [dispatch, currentPage]);

  const handlePageChange = (_event: React.ChangeEvent<unknown>, value: number) => {
    dispatch(fetchVacationsStart());
    // The actual page change will be handled by the useEffect above
  };

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="60vh">
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Grid container spacing={3}>
        {vacations.map(vacation => (
          <Grid item xs={12} sm={6} md={4} key={vacation.id}>
            <VacationCard vacation={vacation} isAdmin={user?.role === 'admin'} />
          </Grid>
        ))}
      </Grid>
      {totalPages > 1 && (
        <Box display="flex" justifyContent="center" mt={4}>
          <Pagination
            count={totalPages}
            page={currentPage}
            onChange={handlePageChange}
            color="primary"
          />
        </Box>
      )}
    </Container>
  );
};

export default VacationList; 