import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useFormik } from 'formik';
import * as yup from 'yup';
import {
  Box,
  Button,
  TextField,
  Typography,
  Paper,
  FormControl,
  InputLabel,
  OutlinedInput,
  InputAdornment,
} from '@mui/material';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import heLocale from 'date-fns/locale/he';
import axios from 'axios';
import { useAppDispatch } from '../../hooks/redux';
import { fetchVacationsSuccess } from '../../store/slices/vacationSlice';

const validationSchema = yup.object({
  destination: yup.string().required('שדה חובה'),
  description: yup.string().required('שדה חובה'),
  startDate: yup.date().required('שדה חובה'),
  endDate: yup
    .date()
    .min(yup.ref('startDate'), 'תאריך סיום חייב להיות אחרי תאריך התחלה')
    .required('שדה חובה'),
  price: yup
    .number()
    .min(0, 'המחיר חייב להיות חיובי')
    .required('שדה חובה'),
  imageUrl: yup.string().url('כתובת URL לא תקינה').required('שדה חובה'),
});

const AddVacation: React.FC = () => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();

  const formik = useFormik({
    initialValues: {
      destination: '',
      description: '',
      startDate: null,
      endDate: null,
      price: '',
      imageUrl: '',
    },
    validationSchema: validationSchema,
    onSubmit: async (values) => {
      try {
        await axios.post('http://localhost:3001/api/v1/vacations', values);
        const response = await axios.get('http://localhost:3001/api/v1/vacations');
        dispatch(fetchVacationsSuccess(response.data));
        navigate('/');
      } catch (error) {
        console.error('Error adding vacation:', error);
      }
    },
  });

  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: '80vh',
      }}
    >
      <Paper
        elevation={3}
        sx={{
          p: 4,
          width: '100%',
          maxWidth: 600,
        }}
      >
        <Typography variant="h5" component="h1" gutterBottom align="center">
          הוספת חופשה חדשה
        </Typography>
        <form onSubmit={formik.handleSubmit}>
          <TextField
            fullWidth
            id="destination"
            name="destination"
            label="יעד"
            value={formik.values.destination}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={formik.touched.destination && Boolean(formik.errors.destination)}
            helperText={formik.touched.destination && formik.errors.destination}
            margin="normal"
          />
          <TextField
            fullWidth
            id="description"
            name="description"
            label="תיאור"
            multiline
            rows={4}
            value={formik.values.description}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={formik.touched.description && Boolean(formik.errors.description)}
            helperText={formik.touched.description && formik.errors.description}
            margin="normal"
          />
          <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={heLocale}>
            <Box sx={{ display: 'flex', gap: 2, my: 2 }}>
              <DatePicker
                label="תאריך יציאה"
                value={formik.values.startDate}
                onChange={(value) => formik.setFieldValue('startDate', value)}
                slotProps={{
                  textField: {
                    fullWidth: true,
                    error: formik.touched.startDate && Boolean(formik.errors.startDate),
                    helperText: formik.touched.startDate && formik.errors.startDate,
                  },
                }}
              />
              <DatePicker
                label="תאריך חזרה"
                value={formik.values.endDate}
                onChange={(value) => formik.setFieldValue('endDate', value)}
                slotProps={{
                  textField: {
                    fullWidth: true,
                    error: formik.touched.endDate && Boolean(formik.errors.endDate),
                    helperText: formik.touched.endDate && formik.errors.endDate,
                  },
                }}
              />
            </Box>
          </LocalizationProvider>
          <FormControl fullWidth margin="normal">
            <InputLabel htmlFor="price">מחיר</InputLabel>
            <OutlinedInput
              id="price"
              name="price"
              type="number"
              value={formik.values.price}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              error={formik.touched.price && Boolean(formik.errors.price)}
              endAdornment={<InputAdornment position="end">₪</InputAdornment>}
              label="מחיר"
            />
            {formik.touched.price && formik.errors.price && (
              <Typography color="error" variant="caption">
                {formik.errors.price}
              </Typography>
            )}
          </FormControl>
          <TextField
            fullWidth
            id="imageUrl"
            name="imageUrl"
            label="קישור לתמונה"
            value={formik.values.imageUrl}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={formik.touched.imageUrl && Boolean(formik.errors.imageUrl)}
            helperText={formik.touched.imageUrl && formik.errors.imageUrl}
            margin="normal"
          />
          <Button
            color="primary"
            variant="contained"
            fullWidth
            type="submit"
            sx={{ mt: 3 }}
          >
            הוסף חופשה
          </Button>
        </form>
      </Paper>
    </Box>
  );
};

export default AddVacation;